@import "mixins";


.first-screen {
    width: 100%;
    margin: 128px 0 0 0;
    border-top: 1px solid rgb(224, 219, 219); 
    padding: 72px 0 40px 0;
    @include container {
        margin: 70px 0 0 0;
    }
    @include tablet2 {
        padding: 42px 0 20px 0;
    }
    @include mobile {
        margin: 50px 0 0 0;
    }
    // .first-screen__body

    &__body {
        width: 100%;
        height: 100%;
    }

    // .first-screen__content

    &__content {
        width: 100%;  
        margin: 0 0 128px 0;
        @include container {
            margin: 0 0 50px  0;
        }
        @include mobile {
            margin: 0 0 20px 0;
        }
        @include flex-all-sb;
        @include tablet2 {
            flex-direction: column;
            align-items: flex-start;
        }
    }

    // .first-screen__left

    &__left {
        max-width: 531px;
        @include tablet2 {
            margin: 0 0 20px 0;
            max-width: 100%;
        }
    }

    // .first-screen__subtitle

    &__subtitle {
        font-size: 22px;
        position: relative;
        margin: 0 0 40px 0;
        font-weight: 300;
        text-transform: uppercase;
        padding-left: 64px;
        &::before {
            position: absolute;
            top: 12px;
            left: 0;  
            content: "";
            width: 56px;
            height: 1px;
            background-color: $blueColor;
        }
        @include tablet2 {
            margin: 0 0 10px 0;
        }
        @include mobile {
            font-size: 18px;
            padding-left: 44px;
            &::before {
                top: 10px;
                width: 36px;
            }
        }
    }

    // .first-screen__title

    &__title {
        font-weight: 500;
        font-size: 50px;
        margin: 0 0 40px 0;
        max-width: 450px;
        @include tablet2 {
            margin: 0 0 20px 0;
            max-width: 100%;
        }
        @include mobile {
            font-size: 35px;
        }
    }

    // .first-screen__text

    &__text {
        max-width: 450px;
        font-size: 18px;
        margin: 0 0 56px 0;
        @include tablet2 {
            margin: 0 0 36px 0;
            max-width: 550px;
        }
        @include mobile {
            font-size: 15px;
            margin: 0 0 26px 0;
        }
    }

    // .first-screen__button

    &__button {
        width: 290px;
    }

    // .first-screen__images

    &__images {
    }

    // .first-screen__image

    &__image {
        @include tablet2 {
           width: 100%;
        }
        @include mobile {
            max-height: 300px;
        }
    }

    // .first-screen__row

    &__row {
        width: 100%;
        @include flex-all-sb;
        @include tablet2 {
            flex-direction: column;
        }
    }

    // .first-screen__item

    &__item {
        width: 350px;
        @include tablet2 {
            width: 100%;
        }
       
    }
}



.first-screen-item {
    transition: .3s all ease 0s;
    background-color: #fff;
    height: 135px;
    box-shadow: 0px 8px 24px rgba(125, 125, 165, 0.16), 0px 1px 4px rgba(210, 220, 255, 0.6);
    border-radius: 2px;
    padding: 24px 20px;
    @include flex-all-sb;
    margin: 0 16px 0 0;
    @include container {
        padding: 12px 8px;
    }
    @include tablet2 {
        margin: 0 0 20px 0;
        justify-content: flex-start;
    }
    &:hover {
        box-shadow: 0px 8px 24px rgba(125, 125, 165, 0.3), 0px 1px 4px rgba(210, 220, 255, 0.7);
    }
    &:last-child {
        margin: 0 0 0 0;
    }
    // .first-screen-item__image

    &__image {
        width: 56px;
        height: 56px;
        margin: 0 16px 0 0;
        @include container {
            width: 46px;
         height: 46px;
        }
        @include tablet2 {
            width: 56px;
            height: 56px;
            margin: 0 30px 0 0;
        }
        @include mobile2 {
            width: 46px;
            height: 46px;
            margin: 0 16px 0 0;
        }
    }

    // .first-screen-item__content

    &__content {
        font-size: 18px;
        @include mobile2 {
            font-size: 16px;
        }
    }

    // .first-screen-item__title

    &__title {
        font-weight: 500;
        margin: 0 0 15px 0;
        @include mobile2 {
            margin: 0 0 10px 0;
            font-size: 18px;
        }
    }
 
    // .first-screen-item__text

    &__text {
        font-weight: 300;
    }
}
